import { CustomSVGRenderer } from "./../rendering/customSvgRenderer";
import { Visualizer } from "./visualizer";
const defaultOptions = {
    width: 500,
    height: 500,
    minx: -0.9,
    miny: -0.9,
    svgWidth: 1.8,
    svgHeight: 1.8,
    strokeWidth: 0.02,
};
/**
 * Visualize puzzles with svg in a DOM
 */
export function SVG(element, type, options = {}) {
    options = Object.assign(Object.assign({}, defaultOptions), options);
    if (typeof element === "string") {
        element = document.querySelector(element);
        if (element === null) {
            throw new Error(`Could not find visuzlier element by query selector: ${element}`);
        }
    }
    const renderer = new CustomSVGRenderer(options.width, options.height, options.minx, options.miny, options.svgWidth, options.svgHeight);
    renderer.strokeWidth = "" + options.strokeWidth;
    element.appendChild(renderer.domElement);
    return new Visualizer(renderer, type, options.puzzle);
}
